@import url(https://fonts.googleapis.com/css?family=Gelasio&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  max-width: 2000px;
  margin: 0 auto;
  /*min-height: 100vh;
  max-height: 100vh;*/
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  
  font-family: 'Gelasio', serif;

  background: lightgray;
}
.header {
  width: 100%;
  height: 6vh;
  padding: 0 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: grey;
  box-sizing: border-box;
}

.header-link {
  text-decoration: none;
  color: black;
}

.header h1 {
  margin: 0;
  font-size: 120%;
  color: #ffffff;
}

.header-button-link:not(:first-child) {
  margin-left: 10px;
}

.header-button {
  padding: 3px 15px;
  border: solid 1px #ffffff;
  color: #ffffff;
  background: none;
  cursor: pointer;
  box-shadow: 0 0 5px -2px #000000;
}

@media (max-width: 350px) {
  .header {
    height: 15vh;
    padding: 10px 0;
    box-sizing: border-box;
    flex-direction: column;
  }

  .header h1 {
    margin-bottom: 5px;
  }
}
.footer {
  padding: 5px 10px;
  box-sizing: border-box;
  width: 100%;
  height: 4vh;
  display: flex;
  align-items: center;
  font-size: 70%;
  background: grey;
}
.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.main {
  padding: 0 40px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.auth-header {
  text-align: center;
}

.auth-text {
  text-align: center;
}

.auth-form {
  width: 400px;
  display: flex;
  flex-direction: column;
}

.input {
  display: block;
  margin-bottom: 20px;
}

.input-field {
  display: block;
  width: 100%;
}

.auth-button {
  text-align: center;
}

.auth-btn {
  padding: 6px 30px;
  border: solid 1px #ffffff;
  background: none;
  cursor: pointer;
  box-shadow: 0 0 5px -2px #000000;
}

@media (max-width: 450px) {
  .auth-form {
    width: 90%;
  }
}
main {
  /*padding: 0 30px;*/
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  /*box-sizing: border-box;*/
  height: 90vh;
  /*overflow: auto;*/
}

.main-content-messages {
  position: relative;
  width: 100%;
  height: 85vh;
  padding: 0 30px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: auto;
}

.message-card {
  width: 50%;
  margin-bottom: 20px;
  padding: 0 10px;
  box-sizing: border-box;
  border-radius: 10px;
  border: solid 1px grey;
  background: white;
  box-shadow: 0 2px 5px 0 grey;
}

.message-card-name {
  margin: 0;
  text-decoration: underline;
}

.message-card-text {
  margin: 0;
  overflow: hidden;
  text-align: center;
  font-style: italic;
}

.message-date {
  display: flex;
  justify-content: space-between;
}

.message-date > p {
  margin: 0;
  font-size: 70%;
}


@media (max-width: 850px) {
  .message-card {
    width: 60%;
  }
}

@media (max-width: 700px) {
  .message-card {
    width: 70%;
  }
}

@media (max-width: 400px) {
  .message-card {
    width: 80%;
  }
}

@media (max-width: 350px) {
  main {
    height: 81vh;
  }
  .message-card {
    width: 90%;
  }
}
.send-message {
  height: 15vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: darkgrey;
  /*border-top: solid 1px grey;*/
  box-sizing: border-box;
}

.send-message-field {
  height: 100%;
  width: 40%;
  padding: 10px;
  margin-right: 20px;
  border: solid 1px grey;
  border-bottom: none;
  box-sizing: border-box;
}

.send-message-button {
  padding: 5px 15px;
  box-shadow: 0 0 5px -2px #000000;
  background: transparent;
  border: solid 1px #ffffff;
  color: #ffffff;
  cursor: pointer;
}

@media (max-width: 800px) {
  .send-message-field {
    width: 50%;
  }
}

@media (max-width: 350px) {
  .send-message-field {
    width: 60%;
  }
}
.button-down {
  position: fixed;
  right: 70px;
  top: 70px;
  
  width: 50px;
  height: 50px;
  
  display: flex;
  justify-content: center;
  align-items: center;
  
  border-radius: 50%;
  box-shadow: 0 0 10px -5px #000000;
  border: solid 1px darkgrey;
  color: white;
  cursor: pointer;
  
  opacity: 0;
  z-index: -1;
  -webkit-transition: opacity 0.3s 0s linear;
  transition: opacity 0.3s 0s linear;
}

.button-down-show {
  opacity: 1;
  z-index: 1;
}
